import React from "react";
import { InputGroup } from "react-bootstrap";
import { HiOutlineMail } from "react-icons/hi";
import { HiLockClosed } from "react-icons/hi2";
import styled from "styled-components";

const TeacherLogin = () => {
  return (
    <Section>
      <Form style={{ paddingBottom: "5rem" }}>
        <FormHeader style={{ marginBottom: "5rem" }}>
          Login Your Account
        </FormHeader>
        <InputGroup>
          <Span className="input-group-text" id="Email">
            <HiOutlineMail />
          </Span>
          <Input
            className="form-control"
            type="email"
            placeholder="Email"
            aria-describedby="Email"
            required
          />
        </InputGroup>
        <InputGroup>
          <Span className="input-group-text" id="Pass">
            <HiLockClosed />
          </Span>
          <Input
            className="form-control"
            type="password"
            placeholder="Password"
            aria-describedby="Pass"
            required
          />
        </InputGroup>

        <ButtonGroup>
          <Button style={{ background: "rgba(255, 71, 87,1.0)" }}>Login</Button>
          <Button style={{ background: "rgba(55, 66, 250,1.0)" }}>Login</Button>
        </ButtonGroup>
      </Form>
    </Section>
  );
};

export default TeacherLogin;

export const Section = styled.section`
  height: 95vh;
  margin-top: 75px;
`;
export const Container = styled.div``;
export const Form = styled.form`
  padding: 1rem;
  width: 400px;
  border: 1px solid rgba(95, 39, 205, 01);
  border-radius: 5px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem 0;

  @media (max-width: 410px) {
    width: 100%;
    border: none;
  }
`;

export const FormHeader = styled.h1`
  @media (min-width: 540px) {
    font-size: 1.3rem;
  }
`;

export const Label = styled.label``;
export const Input = styled.input`
  padding: 0.5rem 1rem;
  border: 1px solid rgba(95, 39, 205, 01);


  &:,
  &:focus {
    outline: none;
    border: 1px solid rgba(95, 39, 205, 0.5);
  }
`;

export const Span = styled.span`
  background: rgba(95, 39, 205, 01);
  color: #fff;
  border: 1px solid rgba(95, 39, 205, 01);
`;

export const ButtonGroup = styled.div`
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  margin-top: 0.5rem;
`;

export const Button = styled.button`
  border: none;
  padding: 0.5rem 1rem;
  font-weight: 600;
  min-width: 175px;
  color: #fff;
  margin: 1rem 0;

  &:nth-child(1) {
    margin-right: 5px;
  }
  &:nth-child(2) {
    margin-left: 5px;
  }

  @media (max-width: 410px) {
    min-width: 95%;
    margin: 0.5rem;
  }
`;
